import React, {Component} from 'react';
import './App.css';

import {EditorState, convertToRaw} from 'draft-js';
import Editor from 'draft-js-plugins-editor';
import createMentionPlugin, {defaultSuggestionsFilter} from 'draft-js-mention-plugin';
import createHashtagPlugin from 'draft-js-hashtag-plugin';
import editorStyles from './editorStyles.css';
import mentions from './mentions';
import data from './companiesWithSymbols';

const Entry = (props) => {
  const {
    mention,
    theme,
    searchValue,
    ...parentProps
  } = props;
  const data = JSON.parse(JSON.stringify(mention));
  return (
    <div {...parentProps}>
      <div>
        <p style={{
          margin: '5px'
        }}>{data.symbol}</p>
        <p
          style={{
          color: '#aaa',
          fontSize: 'small',
          margin: '5px'
        }}>{data.name}</p>
      </div>
    </div>
  );
};

function alreadyThere(arr, elem) {
  let found = false;
  for (let i = 0; i < arr.length && !found; i++) {
    if (arr[i].name.toLowerCase() === elem.name.toLowerCase()) {
      found = true;
    }
  }
  return found;
}

function flatten(arr) {
  const res = [];
  arr.forEach(e => {
    const elem = {
      name: e[1],
      symbol: e[0]
    };
    if (!alreadyThere(res, elem)) {
      res.push(elem)
    }
  });
  return res;
}

const companies = flatten(data);

const positionSuggestions = ({state, props}) => {
  let transform;
  let transition;

  if (state.isActive && props.suggestions.size > 0) {
    transform = 'scaleY(1)';
    transition = 'all 0.25s cubic-bezier(.3,1.2,.2,1)';
  } else if (state.isActive) {
    transform = 'scaleY(0)';
    transition = 'all 0.25s cubic-bezier(.3,1,.2,1)';
  }

  return {transform, transition};
};

class App extends Component {
  constructor(props) {
    super(props);

    this.mentionPlugin = createMentionPlugin({positionSuggestions});
    this.companiesPlugin = createMentionPlugin({mentionTrigger: "$", positionSuggestions});
    this.hashTagPlugin = createHashtagPlugin();
    this.state = {
      editorState: EditorState.createEmpty(),
      suggestions: mentions,
      companies
    }
  }

  onChange = (editorState) => {
    this.postRawData();
    this.setState({editorState});
  };

  postRawData() {
    const rawData = convertToRaw(this.state.editorState.getCurrentContent());
    window.postMessage(JSON.stringify(rawData), '*');
  }

  onSearchChange = ({value}) => {
    this.setState({
      suggestions: defaultSuggestionsFilter(value, mentions)
    });
  };

  onSearchChangeCompanies = ({value}) => {
    this.setState({
      companies: defaultSuggestionsFilter(value, companies)
    });
  };

  onAddMention = (param) => {
    this.postRawData();
  }

  onAddMentionCompanies = (param) => {
    this.postRawData();
  }

  focus = () => {
    this
      .editor
      .focus();
  };

  render() {
    const {MentionSuggestions} = this.mentionPlugin;
    const CompaniesSuggestions = this.companiesPlugin.MentionSuggestions;
    const plugins = [this.mentionPlugin, this.companiesPlugin, this.hashTagPlugin];

    return (
      <div className='container' onClick={this.focus}>
        <div className={editorStyles.editor}>
          <Editor
            editorState={this.state.editorState}
            onChange={this.onChange}
            plugins={plugins}
            ref={(element) => {
            this.editor = element;
          }}/>
          <MentionSuggestions
            onSearchChange={this.onSearchChange}
            suggestions={this.state.suggestions}
            onAddMention={this.onAddMention}/>
          <CompaniesSuggestions
            entryComponent={Entry}
            onSearchChange={this.onSearchChangeCompanies}
            suggestions={this.state.companies}
            onAddMention={this.onAddMentionCompanies}/>
        </div>
      </div>
    );
  }
}

export default App;



